import React, { Component } from 'react';
import { Badge, Card, CardContent, Grid, Divider } from '@mui/material';
import Medal from './Medal';

class Country extends Component {
    render() {
        const { addMedal, removeMedal, country } = this.props;
        return (
            <Grid sx={{mt: 2}}>
                <Card sx={{minWidth: 350}}>
                    <CardContent>
                        <div className='name-holder'>
                            <Badge badgeContent={country.gold + country.silver + country.bronze} color="secondary">
                                <h3 className='name'>{country.name}</h3>
                            </Badge>
                        </div>
                        <Divider varient='middle' sx={{my: 2}}/>
                            <Medal country={ country } 
                                    addMedal={ addMedal }
                                    removeMedal={ removeMedal }
                                    type="gold">
                            </Medal>
                            <Medal country={ country } 
                                    addMedal={ addMedal }
                                    removeMedal={ removeMedal }
                                    type="silver">
                            </Medal>
                            <Medal country={ country } 
                                    addMedal={ addMedal }
                                    removeMedal={ removeMedal }
                                    type="bronze">
                            </Medal>
                    </CardContent>
                </Card>
            </Grid>
        )
    }
}

export default Country;