import React, {Component} from 'react';
import './App.css';
import {AppBar, Badge, Stack, Typography} from '@mui/material';
import Country from './components/Country';
import AddLocationAltIcon from '@mui/icons-material/AddLocationAlt';

class App extends Component {
    state = {
        countries: [
            { id: 1, name: 'United States', gold: 2, silver: 2, bronze: 3 },
            { id: 2, name: 'China', gold: 3, silver: 1, bronze: 0 },
            { id: 3, name: 'Germany', gold: 0, silver: 2, bronze: 2 }
        ]
    }
    addMedal = (countryId, medalType) => {
        const countriesMutable = [...this.state.countries];
        const index = countriesMutable.findIndex((c) => c.id === countryId);
        countriesMutable[index][medalType] += 1;
        this.setState({ countries:countriesMutable });
    }
    removeMedal = (countryId, medalType) => {
        const countriesMutable = [...this.state.countries];
        const index = countriesMutable.findIndex((c) => c.id === countryId);
        countriesMutable[index][medalType] -= 1;
        this.setState({ countries:countriesMutable });
    }
    getTotalMedals = () => {
        const golds = this.state.countries.reduce((a, b) => a + b.gold, 0);
        const silvers = this.state.countries.reduce((a, b) => a + b.silver, golds);
        return this.state.countries.reduce((a, b) => a + b.bronze, silvers);
    }
    render() {
        return (
            <div className="App">
                <AppBar position="static" sx={{mb: 2, p: 2}}>
                    <Typography variant="h4" color="inherit">
                        <Badge badgeContent={ this.getTotalMedals() } color="secondary">
                            Olympic Medals
                        </Badge>
                    </Typography>
                </AppBar>
                <Stack direction="row" justifyContent="center" alignItems="flex-start" spacing={2}>
                    { this.state.countries.map(country => 
                        <Country 
                            key={ country.id } 
                            country={ country } 
                            addMedal={ this.addMedal }
                            removeMedal={ this.removeMedal }/>
                    )}
                </Stack>
                {/* <Button variant='contained' onClick={this.addCountry} sx={{mt: 2}}><AddLocationAltIcon></AddLocationAltIcon></Button> */}
            </div>
        );
    }
}

export default App;
